export interface IVote {
  id: string;
  period: string;
  user: string;
  task: string;
  count?: number;
}

export type ITask = {
  id: string;
  title: string;
  value: number;
};

export interface ICurrentUser {
  id: string;
  displayName: string;
  email: string;
}

export interface IUser {
  id: string;
  displayName: string;
  color: string;
}

export enum SpaceEventTypes {
  CONNECT = "CONNECT",
  DISCONNECT = "DISCONNECT",
  START_PERIOD = "START_PERIOD",
  END_PERIOD = "END_PERIOD",
  VOTE = "VOTE",
  UN_VOTE = "UN_VOTE",
  UPDATE_VOTE = "UPDATE_VOTE",
}

export interface GameServerToClientEvents {
  [SpaceEventTypes.CONNECT]: (message: any) => void;
}

export interface GameClientToServerEvents {
  [SpaceEventTypes.START_PERIOD]: (message: any) => void;
  [SpaceEventTypes.VOTE]: (message: any) => void;
  [SpaceEventTypes.UN_VOTE]: (message: any) => void;
}
